import React, { Component } from 'react'
import axios from 'axios'

const MyContext = React.createContext();

export default class Context extends Component {
    constructor(){
        super()
        this.state={
            isLogged:"",
            isAdmin:false,
            isTeacher:false,
            user:{},
            course:{},
            setLog:(value)=>{
                this.setState({isLogged:value})
            },
            setUser:(value)=>{
                this.setState({user:value})
            }
        }
    }
    componentDidMount(){
       axios.get(' https://thawing-temple-60747.herokuapp.com/auth/refresh',{withCredentials:true}).then((res)=>{
           console.log(res)
       }).catch((err)=>{
           console.log(err)
       })
       axios.get(' https://thawing-temple-60747.herokuapp.com/auth/dashboard',{withCredentials:true})
        .then((res)=>{
            
        
                this.setState({isLogged:true},()=>{
                    console.log("done")
                })
                
                this.setState({isTeacher:res.data._doc.isTeacher,isAdmin:res.data._doc.isAdmin})
                this.setState({user:res})
          
        }).catch((error)=>{
            this.setState({isLogged:false},()=>{
                console.log("done")
            })
            console.log(error.response)
        })
        axios.get(' https://thawing-temple-60747.herokuapp.com/course/getCourses').then((res)=>{
            this.setState({course:res||""})
        }).catch((error)=>{
            console.log(error.response||"")
        })
    }
    
    render() {
        return (
            <MyContext.Provider value={this.state}>
                {this.props.children}
            </MyContext.Provider>
        )
    }
}

export const context=MyContext;